var string = "<% var imgURL = model.get('headshot') %>\n<img id='avatar' height='150' src=\"<%= imgURL || '/vendor/img/avatar.svg' %>\">\n\n<table>\n  <tr data-attribute='name'>\n    <th>Name:</th>\n    <td title='Double-click to edit'><span class='profile-field' id='first_name'><span class='p'><%= model.get('first_name') %></span><span class='form'><form><input name='first_name' type='text' value='<%= model.get('first_name') %>' /></form></span></span> <span class='profile-field' id='last_name'><span class='p'><%=model.get('last_name') %></span><span class='form'><form><input name='last_name' type='text' value='<%= model.get('last_name') %>' /></form></span></span></td>\n  </tr>\n  <tr data-attribute='username'>\n    <th>Username:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='username'>\n        <span class='p'><%= model.get('username') %></span>\n        <span class='form'><form><input name='username' type='text' value='<%= model.get('username') %>'></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='email'>\n    <th>E-mail:</th>\n    <td>\n      <span class='profile-field' id='email' title='Double-click to edit'>\n        <span class='p'><%= model.get('email') %></span>\n        <span class='form'><form><input name='email' type='email' value='<%= model.get('email') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='birthdate'>\n    <th>Birthdate:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='birthdate'>\n        <span class='p'><%= model.get('birthdate') || 'N/A' %></span>\n        <span class='form'><form><input name='birthdate' type='date' value='<%= model.get('birthdate') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='fach'>\n    <th>Fach:</th>\n    <td title='Double-click to edit'>\n      <% var displayFach = model.Fach ? model.Fach.displayTitle() : 'N/A' %>\n      <span class='profile-field' id='fach'>\n        <span class='p'><%= displayFach %></span>\n        <span class='form'>\n          <form>\n            <% var fach = model.escape('fach') %>\n            <select name='fach'>\n              <option<%- fach === 'Soprano - lyric coloratura' ? ' selected' : '' %>>\n                Soprano - lyric coloratura\n              </option>\n              <option<%- fach === 'Soprano - dramatic coloratura' ? ' selected' : '' %>>\n                Soprano - dramatic coloratura\n              </option>\n              <option<%- fach === 'Soprano - lyric' ? ' selected' : '' %>>\n                Soprano - lyric\n              </option>\n              <option<%- fach === 'Soprano - spinto' ? ' selected' : '' %>>\n                Soprano - spinto\n              </option>\n              <option<%- fach === 'Soprano - dramatic' ? ' selected' : '' %>>\n                Soprano - dramatic\n              </option>\n              <option<%- fach === 'Mezzo-soprano - Rossini' ? ' selected' : '' %>>\n                Mezzo-soprano - Rossini\n              </option>\n              <option<%- fach === 'Mezzo-soprano - lyric' ? ' selected' : '' %>>\n                Mezzo-soprano - lyric\n              </option>\n              <option<%- fach === 'Mezzo-soprano - dramatic' ? ' selected' : '' %>>\n                Mezzo-soprano - dramatic\n              </option>\n              <option<%- fach === 'Contralto' ? ' selected' : '' %>>\n                Contralto\n              </option>\n              <option<%- fach === 'Countertenor' ? ' selected' : '' %>>\n                Countertenor\n              </option>\n              <option<%- fach === 'Tenor leggiero' ? ' selected' : '' %>>\n                Tenor leggiero\n              </option>\n              <option<%- fach === 'Tenor - lyric' ? ' selected' : '' %>>\n                Tenor - lyric\n              </option>\n              <option<%- fach === 'Tenor - spinto' ? ' selected' : '' %>>\n                Tenor - spinto\n              </option>\n              <option<%- fach === 'Heldentenor' ? ' selected' : '' %>>\n                Heldentenor\n              </option>\n              <option<%- fach === 'Lyric baritone' ? ' selected' : '' %>>\n                Lyric baritone\n              </option>\n              <option<%- fach === 'Dramatic baritone' ? ' selected' : '' %>>\n                Dramatic baritone\n              </option>\n              <option<%- fach === 'Lyric bass' ? ' selected' : '' %>>\n                Lyric bass\n              </option>\n              <option<%- fach === 'Basso buffo' ? ' selected' : '' %>>\n                Basso buffo\n              </option>\n              <option<%- fach === 'Basso profondo' ? ' selected' : '' %>>\n                Basso profondo\n              </option>\n              <option<%- fach === 'Other' ? ' selected' : '' %>>\n                Other\n              </option>\n            </select>\n          </form>\n        </span>\n      </span>\n    </td>  \n  </tr>\n  <tr data-attribute='address_1'>\n    <th>Address 1:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='address_1'>\n        <span class='p'><%= model.get('address_1') || 'N/A' %></span>\n        <span class='form'><form><input name='address_1' value='<%= model.get('address_1') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='address_2'>\n    <th>Address 2:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='address_2'>\n        <span class='p'><%= model.get('address_2') || 'N/A' %></span>\n        <span class='form'><form><input name='address_1' value='<%= model.get('address_2') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='city'>\n    <th>City:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='city'>\n        <span class='p'><%= model.get('city') || 'N/A' %></span>\n        <span class='form'><form><input name='city' value='<%= model.get('city') %>' /></form></span>\n      </span>\n    </td>  \n  </tr>\n  <tr data-attribute='state'>\n    <th>State:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='state'>\n        <span class='p'><%= model.get('state') || 'N/A' %></span>\n        <span class='form'>\n          <form>\n            <% var state = model.get('state'); %>\n            <select name='address_1'>\n              <option<%- state === 'AK' ? ' selected' : '' %>>\n                AK\n              </option>\n              <option<%- state === 'AL' ? ' selected' : '' %>>\n                AL\n              </option>\n              <option<%- state === 'AR' ? ' selected' : '' %>>\n                AR\n              </option>\n              <option<%- state === 'AZ' ? ' selected' : '' %>>\n                AZ\n              </option>\n              <option<%- state === 'CA' ? ' selected' : '' %>>\n                CA\n              </option>\n              <option<%- state === 'CO' ? ' selected' : '' %>>\n                CO\n              </option>\n              <option<%- state === 'CT' ? ' selected' : '' %>>\n                CT\n              </option>\n              <option<%- state === 'DE' ? ' selected' : '' %>>\n                DE\n              </option>\n              <option<%- state === 'FL' ? ' selected' : '' %>>\n                FL\n              </option>\n              <option<%- state === 'GA' ? ' selected' : '' %>>\n                GA\n              </option>\n              <option<%- state === 'HI' ? ' selected' : '' %>>\n                HI\n              </option>\n              <option<%- state === 'IA' ? ' selected' : '' %>>\n                IA\n              </option>\n              <option<%- state === 'ID' ? ' selected' : '' %>>\n                ID\n              </option>\n              <option<%- state === 'IL' ? ' selected' : '' %>>\n                IL\n              </option>\n              <option<%- state === 'IN' ? ' selected' : '' %>>\n                IN\n              </option>\n              <option<%- state === 'KS' ? ' selected' : '' %>>\n                KS\n              </option>\n              <option<%- state === 'KY' ? ' selected' : '' %>>\n                KY\n              </option>\n              <option<%- state === 'LA' ? ' selected' : '' %>>\n                LA\n              </option>\n              <option<%- state === 'MA' ? ' selected' : '' %>>\n                MA\n              </option>\n              <option<%- state === 'MD' ? ' selected' : '' %>>\n                MD\n              </option>\n              <option<%- state === 'ME' ? ' selected' : '' %>>\n                ME\n              </option>\n              <option<%- state === 'MI' ? ' selected' : '' %>>\n                MI\n              </option>\n              <option<%- state === 'MN' ? ' selected' : '' %>>\n                MN\n              </option>\n              <option<%- state === 'MO' ? ' selected' : '' %>>\n                MO\n              </option>\n              <option<%- state === 'MS' ? ' selected' : '' %>>\n                MS\n              </option>\n              <option<%- state === 'MT' ? ' selected' : '' %>>\n                MT\n              </option>\n              <option<%- state === 'NC' ? ' selected' : '' %>>\n                NC\n              </option>\n              <option<%- state === 'ND' ? ' selected' : '' %>>\n                ND\n              </option>\n              <option<%- state === 'NE' ? ' selected' : '' %>>\n                NE\n              </option>\n              <option<%- state === 'NH' ? ' selected' : '' %>>\n                NH\n              </option>\n              <option<%- state === 'NJ' ? ' selected' : '' %>>\n                NJ\n              </option>\n              <option<%- state === 'NM' ? ' selected' : '' %>>\n                NM\n              </option>\n              <option<%- state === 'NV' ? ' selected' : '' %>>\n                NV\n              </option>\n              <option<%- state === 'NY' ? ' selected' : '' %>>\n                NY\n              </option>\n              <option<%- state === 'OH' ? ' selected' : '' %>>\n                OH\n              </option>\n              <option<%- state === 'OK' ? ' selected' : '' %>>\n                OK\n              </option>\n              <option<%- state === 'OR' ? ' selected' : '' %>>\n                OR\n              </option>\n              <option<%- state === 'PA' ? ' selected' : '' %>>\n                PA\n              </option>\n              <option<%- state === 'RI' ? ' selected' : '' %>>\n                RI\n              </option>\n              <option<%- state === 'SC' ? ' selected' : '' %>>\n                SC\n              </option>\n              <option<%- state === 'SD' ? ' selected' : '' %>>\n                SD\n              </option>\n              <option<%- state === 'TN' ? ' selected' : '' %>>\n                TN\n              </option>\n              <option<%- state === 'TX' ? ' selected' : '' %>>\n                TX\n              </option>\n              <option<%- state === 'UT' ? ' selected' : '' %>>\n                UT\n              </option>\n              <option<%- state === 'VA' ? ' selected' : '' %>>\n                VA\n              </option>\n              <option<%- state === 'VT' ? ' selected' : '' %>>\n                VT\n              </option>\n              <option<%- state === 'WA' ? ' selected' : '' %>>\n                WA\n              </option>\n              <option<%- state === 'WI' ? ' selected' : '' %>>\n                WI\n              </option>\n              <option<%- state === 'WV' ? ' selected' : '' %>>\n                WV\n              </option>\n              <option<%- state === 'WY' ? ' selected' : '' %>>\n                WY\n              </option>\n            </select>\n          </form>\n        </span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='zip'>\n    <th>Zip:</th>\n    <td title='Double-click to edit'>\n      <span class='profile-field' id='zip'>\n        <span class='p'><%= model.get('zip') || 'N/A' %></span>\n        <span class='form'><form><input name='zip' value='<%= model.get('zip') %>' /></form></span>\n      </span>\n    </td>  \n  </tr>\n  <tr>\n    <th>Member Since:</th>\n    <td><%= model.prettyCreateDate() %></td>\n  </tr>\n</table>";

module.exports = string;
